body .card-item.ant-card {
  background: #FFFFFF;
  border: 1px solid #EAEAEA;
  word-break: break-word;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-weight: 600;
  font-size: 11px;
  color: #989CA8;
  letter-spacing: 0;
  text-align: left;
}
body .card-item.ant-card .card-children {
  width: 100%;
  height: 100%;
  flex: 1 1;
  display: flex;
  flex-direction: column;
}
body .card-item.ant-card.ant-card.center .card-children {
  justify-content: center;
  align-items: center;
  text-align: center;
}
body .card-item.ant-card.condensed-card .card-children {
  flex-direction: row;
}
body .card-item.ant-card.condensed-card .ant-card-body {
  padding: 13px;
  flex-direction: row;
}
body .card-item.ant-card.condensed-card .condensed-card-left {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  align-items: flex-start;
  align-self: flex-end;
}
body .card-item.ant-card.condensed-card .condensed-card-left .condensed-card-title {
  font-weight: 700;
  font-size: 40px;
  line-height: 45px;
  color: #0D1B2A;
  letter-spacing: 0;
}
body .card-item.ant-card.condensed-card .condensed-card-left .condensed-card-description {
  font-weight: 700;
  font-size: 11px;
  line-height: 11px;
  color: #9B9B9B;
  letter-spacing: 0;
  text-transform: uppercase;
}
body .card-item.ant-card.condensed-card .condensed-card-right {
  display: flex;
  width: 63px;
  height: 63px;
  background: #0053FA;
  justify-content: center;
  align-items: center;
}
body .card-item.ant-card.condensed-card .condensed-card-right.red {
  background: #FF5F5F;
}
body .card-item.ant-card.condensed-card .condensed-card-right.green {
  background: #5DBE42;
}
body .card-item.ant-card.condensed-card .condensed-card-right svg {
  min-width: 33px;
  min-height: 33px;
  color: white;
}
body .card-item.ant-card .card-edit {
  display: none;
  width: 25px;
  height: 25px;
  padding: 5px;
  top: 28px;
  left: 75%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  position: absolute;
  cursor: pointer;
}
body .card-item.ant-card .card-edit svg {
  color: #B5B9C5;
}
body .card-item.ant-card .card-close {
  width: 25px;
  height: 25px;
  padding: 5px;
  top: 21px;
  right: 10px;
  text-align: right;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  position: absolute;
  cursor: pointer;
}
body .card-item.ant-card .card-close svg {
  color: #B5B9C5;
}
body .card-item.ant-card .card-remove {
  display: none;
  width: 25px;
  height: 25px;
  padding: 5px;
  position: absolute;
  top: 28px;
  left: 25%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  cursor: pointer;
}
body .card-item.ant-card .card-remove svg {
  color: #FF5F5F;
}
body .card-item.ant-card:hover .card-remove,
body .card-item.ant-card:hover .card-edit {
  display: block;
}
body .card-item.ant-card.shadow {
  box-shadow: 0 10px 50px 0 rgba(0, 116, 249, 0.1);
}
body .card-item.ant-card.primary {
  background: rgba(0, 116, 249, 0.03);
  border: 1px solid rgba(0, 116, 249, 0.1);
  box-shadow: 0 1px 10px 0 rgba(0, 116, 249, 0.1);
}
body .card-item.ant-card.primary .card-close svg {
  color: #0053FA;
}
body .card-item.ant-card .ant-card-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  flex: 1 1;
}
body .card-item.ant-card .ant-avatar {
  background: transparent;
}
body .card-item.ant-card .card-avatar.ant-avatar {
  border-radius: 14px;
  color: white;
  margin-bottom: 10px;
  background-color: #0053FA;
}
body .card-item.ant-card .card-avatar.ant-avatar .ant-avatar-string {
  -webkit-transform: translateX(-50%) !important;
          transform: translateX(-50%) !important;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 10px;
  color: #FFFFFF;
  letter-spacing: 2px;
  background: transparent;
}
body .card-item.ant-card .card-item .space {
  margin-top: 10px;
}
body .card-item.ant-card .card-title {
  width: 100%;
  font-weight: 600;
  font-size: 16px;
  color: #0D1B2A;
  letter-spacing: 0;
  text-align: center;
  line-height: 19px;
  margin-bottom: 24px;
}
body .card-item.ant-card .card-title.left {
  width: 100%;
  -moz-text-align-last: left;
       text-align-last: left;
}
body .card-item.ant-card .card-title.right {
  width: 100%;
  -moz-text-align-last: left;
       text-align-last: left;
}
body .card-item.ant-card .card-row-title {
  font-weight: 600;
  font-size: 10px;
  color: #989CA8;
  letter-spacing: 0;
  text-align: center;
  line-height: 11px;
  margin: 3px 0 15px;
  text-transform: uppercase;
}
body .card-item.ant-card .card-row-item {
  font-weight: 500;
  font-size: 14px;
  color: #0D1B2A;
  letter-spacing: 0;
  text-align: center;
  line-height: 21px;
  margin-bottom: 5px;
}

.chart-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
}
.chart-wrapper .absolute-line-chart {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.chart-wrapper .absolute-line-chart text {
  opacity: 0;
}
.chart-wrapper .pie-legend {
  padding-left: 30px;
  position: relative;
  margin-bottom: 10px;
}
.chart-wrapper .pie-legend > :nth-child(1) {
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  left: 15px;
  top: 27px;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
.chart-wrapper .pie-legend > :nth-child(2) {
  display: block;
  font-weight: 500;
  font-size: 12px;
  color: #989CA8;
  letter-spacing: 0;
}
.chart-wrapper .pie-legend > :nth-child(3) {
  display: block;
  font-weight: 400;
  font-size: 20px;
  color: #0d1b2a;
  letter-spacing: 0;
}
.chart-wrapper .pie-total-info {
  font-weight: 500;
  font-size: 12px;
  line-height: 13px;
  color: #989CA8;
  letter-spacing: 0;
}
.chart-wrapper .pie-total {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  color: #0d1b2a;
  letter-spacing: 0;
  text-align: center;
}


.Dashboard_Actions .ant-btn {
  margin-left: 27px;
  width: 156px;
  padding: 0;
}
.dashboard .ant-steps-item {
  width: 20%;
}
.dashboard .ant-steps {
  padding-top: 15px;
}
.dashboard .ant-steps .ant-steps-item-tail {
  margin: 0 0 0 50%;
}
.dashboard .ant-steps .ant-steps-item-content {
  margin-top: 30px;
  width: 100%;
}
.dashboard .ant-steps .ant-steps-item-icon {
  position: absolute;
  margin-left: 0;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
}
.dashboard .ant-steps-item-tail:after {
  height: 3px;
  width: calc(103%);
  margin-left: 0px;
}
.dashboard .ant-steps-item-tail:after,
.dashboard .ant-steps-item-wait .ant-steps-item-icon > .ant-steps-icon .ant-steps-icon-dot {
  background: #DFDFDF;
}
.dashboard .ant-steps-item-finish > .ant-steps-item-tail:after {
  background: #51DAF3;
}
.dashboard .ant-steps-item:not(:first-child).ant-steps-item-process:before {
  content: "";
  display: inline-block;
  height: 3px;
  border-radius: 1px;
  background: -webkit-gradient(linear, left top, right top, from(#51DAF3), to(#0053FA));
  background: -webkit-linear-gradient(left, #51DAF3 0%, #0053FA 100%);
  background: linear-gradient(to right, #51DAF3 0%, #0053FA 100%);
  top: 2px;
  position: absolute;
  -webkit-transform: translate(-100%, 0);
          transform: translate(-100%, 0);
  width: calc(84%);
}
.dashboard .ant-steps-item-title {
  font-weight: 700;
  font-size: 12px;
  letter-spacing: 0;
  text-align: center;
  display: flex;
  flex-direction: column;
