.Dashboard_Actions .ant-btn {
  margin-left: 27px;
  width: 156px;
  padding: 0;
}
.dashboard .ant-steps-item {
  width: 20%;
}
.dashboard .ant-steps {
  padding-top: 15px;
}
.dashboard .ant-steps .ant-steps-item-tail {
  margin: 0 0 0 50%;
}
.dashboard .ant-steps .ant-steps-item-content {
  margin-top: 30px;
  width: 100%;
}
.dashboard .ant-steps .ant-steps-item-icon {
  position: absolute;
  margin-left: 0;
  left: 50%;
  transform: translate(-50%, 0);
}
.dashboard .ant-steps-item-tail:after {
  height: 3px;
  width: calc(103%);
  margin-left: 0px;
}
.dashboard .ant-steps-item-tail:after,
.dashboard .ant-steps-item-wait .ant-steps-item-icon > .ant-steps-icon .ant-steps-icon-dot {
  background: #DFDFDF;
}
.dashboard .ant-steps-item-finish > .ant-steps-item-tail:after {
  background: #51DAF3;
}
.dashboard .ant-steps-item:not(:first-child).ant-steps-item-process:before {
  content: "";
  display: inline-block;
  height: 3px;
  border-radius: 1px;
  background: linear-gradient(to right, #51DAF3 0%, #0053FA 100%);
  top: 2px;
  position: absolute;
  transform: translate(-100%, 0);
  width: calc(84%);
}
.dashboard .ant-steps-item-title {
  font-weight: 700;
  font-size: 12px;
  letter-spacing: 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.dashboard .ant-steps-item-title svg {
  width: 18px;
  height: 18px;
  margin-right: 5px;
  vertical-align: middle;
  color: inherit;
}
.dashboard .ant-steps-item-process > .ant-steps-item-content > .ant-steps-item-title {
  color: #0053FA;
}
.dashboard .ant-steps-item-finish > .ant-steps-item-content > .ant-steps-item-title {
  color: #1DAEFE;
}
.dashboard .ant-steps-item-process .dot-finish,
.dashboard .ant-steps-item-finish .dot-finish {
  background: white;
  border: 2px solid #51DAF3;
  color: #51DAF3;
}
.dashboard .ant-steps-item-process .dot-finish svg,
.dashboard .ant-steps-item-finish .dot-finish svg {
  color: #51DAF3;
}
.dashboard .ant-steps-item-process .dot-process,
.dashboard .ant-steps-item-finish .dot-process {
  border: 2px solid #0053FA;
  background-color: #0053FA;
}
.dashboard .ant-steps-item-process .dot-process svg,
.dashboard .ant-steps-item-finish .dot-process svg {
  color: white;
}
.dashboard .ant-steps-item-process .dot-finish,
.dashboard .ant-steps-item-finish .dot-finish,
.dashboard .ant-steps-item-process .dot-process,
.dashboard .ant-steps-item-finish .dot-process {
  position: relative;
  z-index: 2;
  width: 35px;
  height: 35px;
  line-height: 33px;
  border-radius: 100%;
  transform: translate(-50%, -14px);
  left: 50%;
}
.dashboard .ant-steps-item-process .dot-finish svg,
.dashboard .ant-steps-item-finish .dot-finish svg,
.dashboard .ant-steps-item-process .dot-process svg,
.dashboard .ant-steps-item-finish .dot-process svg {
  vertical-align: middle;
}
.dashboard .chart-dashboard-line text {
  fill: #AEAEAE !important;
}
.dashboard .chart-dashboard-line,
.dashboard .chart-dashboard-pie {
  width: 100%;
  text-align: left;
}
.dashboard .chart-dashboard-line .chart-wrapper-outer,
.dashboard .chart-dashboard-pie .chart-wrapper-outer {
  height: 250px;
  margin-top: 20px;
  position: relative;
}
.dashboard .chart-dashboard-line text,
.dashboard .chart-dashboard-pie text {
  font-weight: 700;
  font-size: 12px;
  letter-spacing: 0;
}
.dashboard .chart-dashboard-line .chart-dashboard-title,
.dashboard .chart-dashboard-pie .chart-dashboard-title {
  padding-left: 15px;
  font-weight: 500;
  font-size: 20px;
  color: #0D1B2A;
  letter-spacing: 0;
}
.dashboard .chart-dashboard-line .chart-dashboard-solde,
.dashboard .chart-dashboard-pie .chart-dashboard-solde {
  padding-left: 15px;
  font-weight: 700;
  font-size: 40px;
  color: #0d1b2a;
  letter-spacing: 0;
}
.dashboard .chart-dashboard-line .chart-dashboard-description,
.dashboard .chart-dashboard-pie .chart-dashboard-description {
  padding-left: 15px;
  font-weight: 500;
  font-size: 15px;
  color: #AEAEAE;
  letter-spacing: 0;
}
.dashboard .card-dashboard {
  padding-left: 44px;
  padding-right: 44px;
  text-align: center;
}
.dashboard .card-dashboard svg {
  height: 150px;
}
.dashboard .card-dashboard .card-dashboard-title {
  margin-top: 20px;
  font-weight: 700;
  font-size: 16px;
  color: #0D1B2A;
  letter-spacing: 0;
  text-align: center;
}
.dashboard .card-dashboard .card-dashboard-description {
  margin-top: 10px;
  font-weight: 500;
  font-size: 15px;
  color: #AEAEAE;
  letter-spacing: 0;
  text-align: center;
  line-height: 23px;
}
.dashboard .card-dashboard button {
  margin-top: 10px;
}
