.chart-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
}
.chart-wrapper .absolute-line-chart {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.chart-wrapper .absolute-line-chart text {
  opacity: 0;
}
.chart-wrapper .pie-legend {
  padding-left: 30px;
  position: relative;
  margin-bottom: 10px;
}
.chart-wrapper .pie-legend > :nth-child(1) {
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  left: 15px;
  top: 27px;
  transform: translate(-50%, 0);
}
.chart-wrapper .pie-legend > :nth-child(2) {
  display: block;
  font-weight: 500;
  font-size: 12px;
  color: #989CA8;
  letter-spacing: 0;
}
.chart-wrapper .pie-legend > :nth-child(3) {
  display: block;
  font-weight: 400;
  font-size: 20px;
  color: #0d1b2a;
  letter-spacing: 0;
}
.chart-wrapper .pie-total-info {
  font-weight: 500;
  font-size: 12px;
  line-height: 13px;
  color: #989CA8;
  letter-spacing: 0;
}
.chart-wrapper .pie-total {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  color: #0d1b2a;
  letter-spacing: 0;
  text-align: center;
}
